<template>
  <div class="post">
    <div class="layer layer-1">
      <h1 class="post-title">{{ this.postData.title }}</h1>
    </div>
    <div class="layer layer-2">
      <div class="post-content" v-html=postContent></div>
    </div>
    <div class="layer layer-3">
      <button class="form-button form-button-post appreciate-button"
        :class="{ 'form-button-inverted': postData.loggedInUserAppreciated }" >
        {{ postData.loggedInUserAppreciated ? 'Appreciate' : 'Appreciated' }}
      </button>
    </div>
    <div class="layer layer-4">
      <router-link :to="`user/${ postData.owner.userName }`">
        <img class="author-icon" :src=postData.owner.profileAsset.path />
        <div class="author-info">
          <div class="author-info-name">{{ postData.owner.name }}</div>
          <div class="author-info-username">{{ postData.owner.userName }}</div>
        </div>
      </router-link>
      <button class="form-button form-button-user follow-button"
        :class="{ 'form-button-inverted': }">
      
      </button>
    </div>
  </div>
</template>

<script>
import marked from 'marked'

export default {
  name: 'post',

  beforeMount() {
  
    this.populatePost();
  },
  watch: {
    '$route'() {
      
      this.populatePost();
    }
  },

  data: () => ({
    
    postContent: '',
    postData: {
      markdownContent: '',
      title: 'Loading...',
      postViews: 0,
      postAppreciations: 0,
      postKeywords: [],
      
      owner: {
        profileAsset: {
          path: ''
        },
        userName: '',
        name: '',
        description: '',
        privilegeStatus: ''
      }
    }

  }),

  computed: {
    
    currentUserFollowsAuthor() {
      return this.currentUser.follows.find( user => user.userName === postData.owner.userName ) 
    }
  },


  methods: {
    populatePost() {
      
      this.$store.dispatch( 'populatePost', {
        userName: this.$route.params.username,
        postId: this.$route.params.postid

      }).then( postData => {
        
        this.postData = postData;
        this.postContent = marked( this.postData.markdownContent );

      })
    }
  }
}
</script>

<style scoped>
.layer {
  margin-top: 20px;
}
.layer-1 {
  margin-top: 50px;
}
.post-title {
  text-align: center;
  color: #eee;
  margin: auto;
  font-size: 34px;
}
.post-content {
  padding: 18px;
  width: 612px;
  margin: auto;
  background-color: #f8f8f8;
}
.appreciate-button {
  margin: auto;
  display: block;
}
</style>



